body {
	background: #f7f0e6;
	overflow: scroll;
	font-family: "Avenir Next", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
	font-weight: 400;
	font-style: normal;
}

.logo {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.logo a {
	width: 65%;
	height: 65%;
}
.logo a img {
	width: 100%;
	transition: 1s;
}
.logo a img:hover {
	transform: rotate(360deg) scale(1.25);
}

.nav {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-size: 1.7vw;
	letter-spacing: 0.05em;
}
@media screen and (max-width: 767px) {
	.nav {
		font-size: 4vw;
	}
}

.faq-button {
	color: black;
	text-decoration: none;
}

.nav-dropdown {
	position: relative;
	cursor: pointer;
}
.nav-dropdown:hover ul {
	display: block;
}
.nav-dropdown ul {
	position: absolute;
	display: none;
	top: 100%;
	left: 0;
	width: 100%;
	background-color: #ebe0cc;
}
.nav-dropdown ul li {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border: 1px solid rgb(32, 32, 32);
	border-bottom-width: 0;
	font-size: 0.8em;
	line-height: 1.2em;
	text-align: center;
	min-height: 6.25vw;
	padding: 0 1vw;
	color: rgb(32, 32, 32);
	text-decoration: none;
	cursor: pointer;
}
@media screen and (max-width: 767px) {
	.nav-dropdown ul li {
		padding: 3vw 1vw;
	}
}
.nav-dropdown ul li:last-child {
	border-bottom-width: 1px;
}
.nav-dropdown ul li:hover {
	background-color: rgba(255, 255, 255, 0.3);
}
.nav-dropdown ul li.active {
	background-color: rgba(255, 255, 255, 0.5);
}
.nav-dropdown ul li.clicked {
	background-color: rgb(255, 255, 255);
}
.nav-dropdown ul li small {
	display: block;
	font-size: 0.8em;
	opacity: 0.5;
}

.filled-spacer {
	background-color: #620505;
}

.filled-blue {
	background-color: #65a2eb;
}

.filled-red {
	background-color: #d3253d;
}

.filled-teal {
	background-color: #4ad9bf;
}

.filled-purple {
	background-color: #9482db;
}

.filled-orange {
	background-color: #ff7647;
}

.filled-yellow {
	background-color: #fcaf32;
}

.main-title {
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 0 0 0 2vw;
}
@media screen and (max-width: 767px) {
	.main-title {
		padding: 0 0 0 6vw;
	}
}
.main-title h1 {
	font-weight: 300;
	font-size: 11vw;
}
@media screen and (max-width: 767px) {
	.main-title h1 {
		font-size: 20vw;
	}
}
.main-title h1 div:nth-child(2) {
	font-size: 0.85em;
	letter-spacing: 0.04em;
}
.main-title h1 div:nth-child(2):hover {
	letter-spacing: 0.07em;
}
.main-title h1 div:nth-child(3) {
	font-size: 0.7em;
}
.main-title h1 div {
	transition: letter-spacing 0.3s;
}
.main-title h1 div:hover {
	letter-spacing: 0.03em;
}
.main-title h1 span.l {
	margin-left: 0em;
	margin-right: -0.06em;
}
.main-title h1 span.o {
	margin-left: -0.08em;
	margin-right: -0.08em;
}

.main-title-mobile {
	text-align: center;
	display: flex;
	justify-content: center;
	flex-direction: column;
}
.main-title-mobile.main-title-mobile-student {
	font-size: 12vw;
	text-transform: uppercase;
	letter-spacing: 0.1em;
}
.main-title-mobile.main-title-mobile-portfolio {
	font-size: 19vw;
	letter-spacing: 0.1em;
}
.main-title-mobile.main-title-mobile-review {
	font-size: 13vw;
	text-transform: uppercase;
	letter-spacing: 0.15em;
}

.big-letter {
	position: relative;
	font-size: 9vw;
	text-transform: uppercase;
	text-align: center;
	overflow: hidden;
}
.big-letter span {
	position: absolute;
	top: 53%;
	width: 100%;
	transform: translate(-50%, -50%);
}
.big-letter.animate span {
	animation-name: big-letter-right;
	animation-duration: 1s;
	animation-fill-mode: both;
}
.big-letter.big-letter-c1.animate span {
	animation-name: big-letter-down;
}
.big-letter.big-letter-i.animate span {
	animation-name: big-letter-up;
}
.big-letter.big-letter-r.animate span {
	animation-name: big-letter-left;
}
.big-letter.big-letter-u.animate span {
	animation-name: big-letter-left;
}
.big-letter.big-letter-s.animate span {
	animation-name: big-letter-down;
}

.star-line {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}
.star-line img {
	height: 50%;
}
.star-line.star-line-corner img {
	height: 25%;
}

.featured-person {
	background-size: contain;
	background-position: bottom center;
	background-repeat: no-repeat;
	position: relative;
	overflow: hidden;
}
.featured-person.featured-person-orange {
	background-color: #ff8961;
}
.featured-person.featured-person-blue {
	background-color: #85b5ef;
}
.featured-person.featured-person-peach {
	background-color: #dd3e54;
}
.featured-person img {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	transform: translateX(110%);
}
.featured-person img:nth-child(1) {
	transform: translateX(0%);
}

.spacer-top-of-students {
	display: flex;
}
.spacer-top-of-students div {
	flex-grow: 1;
	display: flex;
	justify-content: center;
	align-items: center;
}
.spacer-top-of-students div img {
	height: 15%;
}

.student {
	display: flex;
	flex-direction: column;
}
@media screen and (max-width: 767px) {
	.student:nth-child(odd) figcaption {
		order: -1;
	}
}
.student.student-purple figure {
	background-color: #9482db;
}
.student.student-blue figure {
	background-color: #65a2eb;
}
.student.student-green figure {
	background-color: #4ad9bf;
}
.student.student-orange figure {
	background-color: #ff7647;
}
.student.student-yellow figure {
	background-color: #fcaf32;
}
.student.student-dark-blue figure {
	background-color: #65a2eb;
}
.student.student-red figure {
	background-color: #d3253d;
}
.student .student-content {
	width: 100%;
	display: flex;
}
.student .student-content figure {
	width: 50%;
	position: relative;
	height: 0;
